import { registerBlockType } from '@wordpress/blocks';
import { __ } from '@wordpress/i18n';
import './editor.scss';
import './style.css';

registerBlockType('card-block/notification', {
    "apiVersion": 2,
    "name": "card-block/notification",
    "title": "Notification",
    "category": "CardBlock",
    "icon": "smiley",

    edit: function(props) {
        console.log(props);
        return (
            <div className="notification-block">
                <h2 className='heading'>Notification</h2>
            </div>
        );
    },
    save: function(props) {
        console.log(props);
        return (
            <div className="notification-block">
                <h2 className='heading'>Notification</h2>
            </div>
        );
    },
});
